import React, { Component } from 'react';
import {Link} from 'react-router-dom';
class Project extends Component {
    render() {
        return (
            <section className="get-project s4">
                <div className="container">
                    <div className="row">
                        <div className="col-lg-6">
                            <div className="featured-post animate-element wow delay5 fadeZooming" data-wow-delay="0.5s">
                            <img src="images/section/74.png" alt="images" />
                            </div>
                        </div>
                        <div className="col-lg-6">
                            <div className="content-inner animate-element wow delay5 fadeInUp" data-wow-delay="0.5s">
                                <h2 className="title mg-b14 color-d11">Contact Me!</h2>
                                <div className="site-list has-border">
                                    <a href="mailto:dlosilla.work@gmail.com" className="bg-s1"><i className="fa fa-envelope" aria-hidden="true" /></a>
                                    <a href="https://www.linkedin.com/in/david-losilla" className="bg-s2"><i className="fa fa-linkedin" aria-hidden="true" /></a>
                                    <a href="https://github.com/DavidLosilla" className="bg-s1"><i className="fa fa-github" aria-hidden="true" /></a>    
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </section>
        );
    }
}

export default Project;
